import * as React from 'react';
import styled from 'styled-components';
import { modalImg, modalDesktopImg, modalTabletImg } from 'assets/images/png';
import { device } from 'lib/constants';

export const Modal: React.FC<{
  isVisible: boolean;
  onClose(event: React.SyntheticEvent): void;
}> = ({ isVisible, onClose, children }) => (
  <>
    <BackgroundDark isVisible={isVisible} />
    <ModalBackground isVisible={isVisible}>
      <CloseBtn onClick={onClose} />
      <Content>{children}</Content>
    </ModalBackground>
  </>
);

interface MapProps {
  isVisible: boolean;
}

const map = (props: MapProps) => ({
  'data-visible': props.isVisible,
});

export const ModalBackground = styled.div.attrs(map)<MapProps>`
  width: 290px;
  height: 285px;
  background-image: url(${modalImg});
  background-size: cover;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 5;
  opacity: 0;
  visibility: hidden;

  &[data-visible='true'] {
    opacity: 1;
    visibility: inherit;
  }

  @media ${device.tablet} {
    width: 546px;
    height: 331px;
    background-image: url(${modalTabletImg});
    padding: 0;
  }

  @media ${device.desktop} {
    width: 770px;
    height: 383px;
    background-image: url(${modalDesktopImg});
  }
`;

export const BackgroundDark = styled.div.attrs(map)<MapProps>`
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.15s ease-in-out;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;

  &[data-visible='true'] {
    opacity: 1;
    visibility: inherit;
  }
`;

export const Content = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 0 25px;

  @media ${device.tablet} {
    padding: 0 66px;
  }

  @media ${device.desktop} {
    padding: 0 115px;
  }
`;

export const CloseBtn = styled.div`
  width: 44px;
  height: 44px;
  box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.17), 0 0 0 0 rgba(0, 0, 0, 0.16),
    inset 0 1px 3px 0 rgba(0, 0, 0, 0.05);
  border: solid 2px #443686;
  background-image: radial-gradient(circle at 50% 50%, #614fb5, #504099 71%);
  position: absolute;
  top: 18px;
  right: 18px;
  border-radius: 50%;
  ::after {
    content: '';
    width: 15px;
    display: block;
    height: 2px;
    background: #fff;
    transform: rotate(45deg);
    border-radius: 1.5px;
    position: absolute;
    top: 21px;
    right: 14px;
  }
  ::before {
    display: block;
    content: '';
    width: 15px;
    height: 2px;
    background: #fff;
    transform: rotate(-45deg);
    border-radius: 1.5px;
    position: absolute;
    top: 21px;
    right: 14px;
  }
`;
