import * as React from 'react';
import styled from 'styled-components';
import { Title, TextBody } from 'ui/atoms';
import ovalImg from 'assets/images/svg/oval.svg';
import { device } from 'lib/constants';

export const List: React.FC<{
  items: { title: string; number: number; body: string }[];
}> = ({ items }) => (
  <>
    {items.map(({ title, body, number }) => (
      <Item key={number}>
        <Oval>
          <Number>{number}</Number>
        </Oval>
        <Text>
          <TitleBlock>
            <Title size="s">{title}</Title>
          </TitleBlock>
          <TextBodyBlock>
            <TextBody color="secondary">{body}</TextBody>
          </TextBodyBlock>
        </Text>
      </Item>
    ))}
  </>
);

export const Number = styled.div`
  text-shadow: 0 0 12px rgba(255, 248, 221, 0.55);
  background-image: radial-gradient(circle at 44% 50%, #ffbf5d, #ff9a00 32%);
  font-size: 15px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.4;
  letter-spacing: normal;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
`;
export const Text = styled.div`
  text-align: left;
  width: 220px;
  font-size: 12px;
  font-weight: 500;
  padding-left: 18px;
  padding-bottom: 30px;

  @media ${device.tablet} {
    padding-left: 37px;
    width: 476px;
  }
  @media ${device.desktop} {
    width: 700px;
  }
`;

export const Item = styled.div`
  display: flex;
  :last-child {
    ${Text} {
      padding-bottom: 24px;
      @media ${device.desktop} {
        padding-bottom: 117px;
      }
    }
  }
`;

export const TitleBlock = styled.div`
  padding-bottom: 9px;
  ${Title} {
    text-align: left;
  }

  @media ${device.tablet} {
    padding-bottom: 13px;
    ${Title} {
      line-height: 1.29;
    }
  }
  @media ${device.desktop} {
    padding-bottom: 12px;

    div {
      line-height: 1.29 !important;
      font-size: 21px !important;
    }
  }
`;

export const Oval = styled.div`
  background-image: url(${ovalImg});
  background-size: 100% 100%;
  width: 33px;
  height: 33px;
  object-fit: contain;
  margin-top: 2px;
`;

export const TextBodyBlock = styled.div`
  p {
    line-height: 1.5;
  }
  @media ${device.tablet} {
    width: 465px;
  }
  @media ${device.desktop} {
    width: 647px;
  }
`;
