import styled from 'styled-components';
import { device } from 'lib/constants';

interface MapProps {
  size: 'l' | 'm' | 's';
}

const map = (props: MapProps) => ({
  'data-size': props.size || 'm',
});

export const Title = styled.div.attrs(map)<MapProps>`
  text-align: center;
  color: var(--primary);
  font-weight: 800;
  white-space: pre-wrap;
  margin: 0 auto;

  &[data-size='l'] {
    font-size: 24px;
    line-height: 1.25;
    @media ${device.tablet} {
      font-size: 36px;
      line-height: 1.34;
      text-align: left;
    }
    @media ${device.desktop} {
      font-size: 54px;
      line-height: 1.15;
    }
  }

  &[data-size='m'] {
    font-size: 18px;
    line-height: 1.34;
    @media ${device.tablet} {
      font-size: 30px;
      line-height: 1.2;
      width: 100%;
    }
    @media ${device.desktop} {
      font-size: 36px;
      line-height: 1.17;
    }
  }

  &[data-size='s'] {
    line-height: 1.8;
    font-size: 15px;
    @media ${device.tablet} {
      font-size: 21px;
      line-height: 2;
      width: 100%;
    }
  }
`;
