import * as React from 'react';
import styled from 'styled-components';
import { TextBody, Title } from 'ui/atoms';
import { Content } from 'content';
import { MenuItems } from './menu-items';
import { device } from 'lib/constants';

interface SidebarProps {
  isVisible: boolean;
  toggler(event: React.SyntheticEvent): void;
  menu: { text: string; link: string }[];
}

export const Sidebar: React.FC<SidebarProps> = ({
  isVisible,
  toggler,
  menu,
}) => {
  return (
    <>
      <BurgerBlock onClick={toggler}>
        <Burger isVisible={isVisible} />
      </BurgerBlock>
      <Background isVisible={isVisible} />
      <Aside isVisible={isVisible}>
        <AsideContent>
          <AsideMenu items={menu} className="sidemenu" onMenuClick={toggler} />
        </AsideContent>
        <AsideFooter>
          <Title size="s">{Content.sidemenuEmail}</Title>
          <TextBody>{Content.sidemenuLicence}</TextBody>
        </AsideFooter>
      </Aside>
    </>
  );
};

interface MapProps {
  isVisible: boolean;
}

const map = (props: MapProps) => ({
  'data-visible': props.isVisible,
});

export const Background = styled.div.attrs(map)<MapProps>`
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.15s ease-in-out;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;

  &[data-visible='true'] {
    opacity: 1;
    visibility: inherit;
  }
`;

export const Aside = styled.aside.attrs(map)<MapProps>`
  position: fixed;
  left: 0;
  top: 0;
  max-width: 306px;
  width: 100%;
  min-height: 100vh;
  height: 100%;
  transition: all 0.25s ease-in-out;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.2);
  z-index: 2;
  background-color: var(--main);
  opacity: 0;
  visibility: hidden;

  &[data-visible='true'] {
    opacity: 1;
    visibility: inherit;
  }
`;

export const AsideContent = styled.div`
  padding: 24px 18px;
  display: flex;
  flex-wrap: wrap;

  span {
    display: block;
    margin: 12px 0;
    font-size: 1.6rem;
  }
`;

export const AsideMenu = styled(MenuItems)`
  display: flex;
  flex-direction: column;
  padding-top: 89px;

  a:first-child {
    font-size: 15px;
  }

  li {
    padding: 15px 0;
  }

  a {
    font-size: 15px;
    font-weight: 500;
    opacity: 1;
  }
`;

export const Burger = styled.div.attrs(map)<MapProps>`
  width: 25px;
  height: 3px;
  border-radius: 1.5px;
  background-color: var(--primary);
  position: fixed;
  top: 27px;
  left: 19px;
  z-index: 3;
  transition: all 0.25s ease-in-out;

  &[data-visible='true'] {
    transform: rotate(45deg);
    ::after {
      transform: rotate(-90deg);
      top: 0;
    }
  }

  ::after {
    bottom: -8px;
    content: '';
    display: block;
    width: 25px;
    height: 3px;
    background-color: var(--primary);
    position: absolute;
    border-radius: 1.5px;
  }

  @media ${device.tablet} {
    display: none;
  }
`;

export const BurgerBlock = styled.div`
  display: block;
  width: 25px;
  height: 25px;
  position: fixed;
  top: 27px;
  left: 19px;
  z-index: 4;
  @media ${device.tablet} {
    display: none;
  }
`;

export const AsideFooter = styled.div`
  position: absolute;
  bottom: 15px;
  left: 15px;

  div {
    margin: initial;
    text-align: left;
    padding-bottom: 12px;
  }
`;
