import * as React from 'react';
import {
  ButtonDescription,
  Description,
  Feedback,
  Header,
  License,
  Line,
  Shape,
  SubscribePage,
} from './style';
import { Title, TextBody } from 'ui/atoms';
import { FormFeedback } from 'ui/molecules';
import { Layout } from 'ui/templates';
import { bgSubscribe, bgTabletSubscribe } from 'assets/images/background';
import MailchimpSubscribe from 'react-mailchimp-subscribe';
import shape from 'assets/images/svg/shape5.svg';
import { Content } from 'content';
import { url } from 'lib/constants';

export const Subscribe: React.FC = () => (
  <SubscribePage className="page" data-id="subscribe">
    <Layout
      background={bgSubscribe}
      smallBackground={bgTabletSubscribe}
      id="subscribe"
    >
      <Header>
        <Title size="m">{Content.subscribeTitle}</Title>
      </Header>
      <Description>
        <TextBody color="basic">{Content.subscribeText}</TextBody>
      </Description>
      <Feedback>
        <MailchimpSubscribe
          url={url}
          render={({ subscribe, status, message }) => (
            <FormFeedback
              status={status}
              message={message}
              onValidated={(formData) => subscribe(formData)}
            />
          )}
        />
      </Feedback>
      <ButtonDescription>
        <TextBody color="secondary">{Content.subscribeBottomText}</TextBody>
      </ButtonDescription>
      <Line />
      <License>
        <TextBody color="secondary" size="s">
          {Content.subscribeLicense}
        </TextBody>
      </License>
    </Layout>
    <Shape src={shape} />
  </SubscribePage>
);
