import styled from 'styled-components';
import { device } from 'lib/constants';

interface MapProps {
  color?: 'basic' | 'secondary';
  isBold?: boolean;
  size?: 'm' | 's' | 'xs';
}

const map = (props: MapProps) => ({
  'data-color': props.color || 'secondary',
  'data-bold': props.isBold,
  'data-size': props.size || 'm',
});

export const TextBody = styled.p.attrs(map)<MapProps>`
  font-size: 12px;
  font-weight: 500;
  line-height: 1.75;
  padding: 0;
  margin: 0;

  @media ${device.tablet} {
    font-size: 15px;
  }

  @media ${device.desktop} {
    font-size: 18px;
    line-height: 1.5;
  }

  &[data-color='basic'] {
    color: var(--basic);

    @media ${device.tablet} {
      line-height: 1.8;
    }

    @media ${device.desktop} {
      line-height: 1.5;
    }
  }

  &[data-color='secondary'] {
    color: var(--secondary);

    @media ${device.tablet} {
      line-height: 1.6;
    }

    @media ${device.desktop} {
      line-height: 1.5;
    }
  }

  &[data-bold='true'] {
    font-weight: bold;
    line-height: 1.8;
  }

  &[data-size='s'] {
    font-size: 12px;

    @media ${device.desktop} {
      font-size: 15px;
      line-height: 1.75;
    }
  }

  &[data-size='xs'] {
    font-size: 12px;

    @media ${device.tablet} {
      line-height: 1.25;
    }
  }
`;
