import * as React from 'react';
import styled from 'styled-components';
import appStoreImg from 'assets/images/svg/app-store-button.svg';
import googlePlayImg from 'assets/images/svg/google-play-button.svg';
import { device } from 'lib/constants';
import ScrollIntoView from 'react-scroll-into-view';

interface ButtonsDownloadProps {
  iosEvent(): void;
  androidEvent(): void;
}

export const ButtonsDownload: React.FC<ButtonsDownloadProps> = ({
  iosEvent,
  androidEvent,
}) => (
  <Buttons>
    <div onClick={iosEvent}>
      <ScrollIntoView selector="#subscribe">
        <AppStoreButton />
      </ScrollIntoView>
    </div>
    <div onClick={androidEvent}>
      <ScrollIntoView selector="#subscribe">
        <GooglePlayButton />
      </ScrollIntoView>
    </div>
  </Buttons>
);

export const AppStoreButton = styled.div`
  margin: 0 5px;
  background-image: url(${appStoreImg});
  width: 211px;
  height: 69px;
  background-size: cover;
  &:hover {
    cursor: pointer;
  }
  @media ${device.tablet} {
    width: 294px;
    height: 89px;
  }
`;

export const GooglePlayButton = styled.div`
  padding: 0 5px;
  background-image: url(${googlePlayImg});
  width: 211px;
  height: 69px;
  background-size: cover;
  &:hover {
    cursor: pointer;
  }
  @media ${device.tablet} {
    width: 294px;
    height: 89px;
  }
`;

export const Buttons = styled.div`
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  @media ${device.tablet} {
    flex-direction: row;
  }
`;
