import * as React from 'react';
import styled from 'styled-components';
import { device } from 'lib/constants';
import { Content } from 'content';
import { ButtonSubscribe } from 'ui/molecules';
import { Sidebar } from './sidebar';
import { MenuItems } from './menu-items';

interface MenuProps {
  items: { text: string; link: string }[];
}

export const Menu: React.FC<MenuProps> = ({ items }) => {
  const [isVisible, setVisible] = React.useState(false);
  const [menuView, setMenuView] = React.useState<'fixed' | null>(null);

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  });

  const handleScroll = React.useCallback(() => {
    const { scrollY } = window;
    if (scrollY > 121) {
      setMenuView('fixed');
    }
    if (scrollY < 120) {
      setMenuView(null);
    }
  }, []);

  return (
    <TopBlock scrollData={menuView}>
      <Nav>
        <MenuItems items={items} className="menu" />
        <ButtonMenu>
          <ButtonSubscribe>{Content.buttonMenuText}</ButtonSubscribe>
        </ButtonMenu>
      </Nav>
      <Sidebar
        isVisible={isVisible}
        toggler={() => {
          setVisible(!isVisible);
        }}
        menu={items}
      />
    </TopBlock>
  );
};

export const Nav = styled.nav`
  padding: 24px 0;
  display: none;
  width: 100%;
  align-items: center;
  justify-content: space-between;

  @media ${device.tablet} {
    margin: 0 auto;
    display: flex;
    max-width: 768px;
  }

  @media ${device.desktop} {
    max-width: 1440px;
  }
`;

export const ButtonMenu = styled.div`
  display: none;
  padding-right: 64px;

  button {
    height: 48px;
    font-size: 16px;
    width: 170px;
    padding: 0 10px;
  }
`;

interface MapProps {
  scrollData: 'fixed' | null;
}

const map = (props: MapProps) => ({
  'data-scrolled': props.scrollData,
});

export const TopBlock = styled.div.attrs(map)<MapProps>`
  position: absolute;
  z-index: 2;
  width: 100%;

  @media ${device.tablet} {
    width: 100%;
    display: flex;
    justify-content: flex-start;

    &[data-scrolled='fixed'] {
      position: fixed;
      box-shadow: 0 2px 14px 0 rgba(0, 0, 0, 0.04),
        inset 0 -1px 0 0 rgba(0, 0, 0, 0.05);
      max-height: 95px;
      transition: 0.3s;
      background: #2a2a4f;
      @media ${device.tablet} {
        ${ButtonMenu} {
          display: flex;
        }
      }
    }
  }
`;
