import * as React from 'react';
import styled from 'styled-components';
import { Button, Title, TextBody, TextStar } from 'ui/atoms';
import { device } from 'lib/constants';
import { Modal } from './modal';
import { Content } from 'content';
import { formEvent } from 'lib/events';

interface FormFeedbackProps {
  status: 'success' | 'error' | 'sending' | null;
  message: string | null;
  onValidated(data: { EMAIL: string }): void;
}

export const FormFeedback: React.FC<FormFeedbackProps> = ({
  status,
  message,
  onValidated,
}) => {
  const [isVisible, setVisible] = React.useState(false);
  const [email, setEmail] = React.useState('');
  const [error, setError] = React.useState('');
  const [isSend, setSend] = React.useState(false);

  React.useEffect(() => {
    if (isSend && status === 'success') {
      setVisible(true);
      setError('');
      setSend(false);
      setEmail('');
    }

    if (status === 'error') {
      setError('This email is already subscribed');
      setSend(false);
    }
  }, [status, isSend]);

  const onClick = (event: React.SyntheticEvent) => {
    event.preventDefault();
    const re = /\S+@\S+\.\S+/;

    if (email && re.test(email)) {
      setSend(true);
      onValidated({
        EMAIL: email,
      });
      formEvent();
    } else {
      setError('Please enter a valid email address');
    }
  };

  const onChange = (event: React.FormEvent<HTMLInputElement>) => {
    setError('');
    setEmail(event.currentTarget.value);
  };
  return (
    <Form>
      <Feedback onSubmit={onClick}>
        <Input
          placeholder="Please, enter your email"
          value={email}
          onChange={onChange}
          type="email"
        />
        <ButtonBlock>
          <Button type="submit">Send</Button>
        </ButtonBlock>
      </Feedback>
      <TextBody color="basic" size="s">
        {error}
      </TextBody>
      <Modal
        isVisible={isVisible}
        onClose={() => {
          setVisible(false);
        }}
      >
        <TitleBlock>
          <Title size="m">{Content.subscribePopupHeader}</Title>
        </TitleBlock>
        <TextBlock>
          <TextBody>{Content.subscribePopupText}</TextBody>
        </TextBlock>
        <BottomText>
          <TextBody color="basic" isBold>
            Please, check your email, we’ve
          </TextBody>
          <TextLine>
            <TextBody color="basic" isBold>
              already sent
            </TextBody>
            <TextStar>your invite</TextStar>
          </TextLine>
        </BottomText>
      </Modal>
    </Form>
  );
};

export const Form = styled.div`
  display: flex;
  flex-direction: column;
  p {
    padding-top: 5px;
  }
`;

export const Input = styled.input`
  width: 290px;
  height: 50px;
  border-radius: 22.5px;
  border: solid 2.5px var(--form-border);
  background-color: var(--form);
  padding-left: 15px;
  box-sizing: border-box;
  font-size: 12px;
  font-weight: 500;
  outline: none;
  color: var(--primary);

  &::placeholder {
    color: var(--primary);
    opacity: 0.6;
  }
  @media ${device.tablet} {
    width: 454px;
    height: 54px;
  }

  @media ${device.desktop} {
    width: 570px;
    height: 65px;
    border-radius: 33px;
    font-size: 15px;
    font-weight: 500;
    padding-left: 25px;
  }
`;

export const Feedback = styled.form`
  display: flex;
  align-items: center;
  position: relative;
`;

export const ButtonBlock = styled.div`
  position: absolute;
  right: 0;
  display: flex;
  flex-direction: column;
  width: 103px;
  height: 45px;
  @media ${device.tablet} {
    width: 172px;
    height: 54px;
  }
  @media ${device.tablet} {
    width: 170px;
    height: 65px;
  }
`;

export const TitleBlock = styled.div`
  padding-bottom: 15px;

  @media ${device.tablet} {
    padding-bottom: 18px;
    ${Title} {
      line-height: 1.25;
    }
  }
  @media ${device.desktop} {
    ${Title} {
      font-size: 30px;
      line-height: 1.4;
    }
  }
`;

export const TextBlock = styled.div`
  padding-bottom: 27px;

  @media ${device.tablet} {
    padding-bottom: 30px;
  }
  @media ${device.desktop} {
    padding-bottom: 49px;
    ${TextBody} {
      line-height: 1.5;
    }
  }
`;

export const BottomText = styled.div`
  display: flex;
  flex-direction: column;
  ${TextBody} {
    padding-top: 0px;
    line-height: 1.5;
  }
  ${TextStar} {
    padding: 0;
    margin: 0;
    line-height: 1.5;
    padding-left: 5px;
  }
  @media ${device.tablet} {
    flex-direction: row;
    ${TextBody} {
      font-size: 15px;
      line-height: 1.8;
    }
    ${TextStar} {
      font-size: 15px;
      line-height: 1.8;
      padding-left: 5px;
    }
  }
`;

export const TextLine = styled.div`
  display: flex;
  justify-content: center;
  @media ${device.tablet} {
    padding-left: 5px;
  }
`;
