import * as React from 'react';
import styled from 'styled-components';
import arrow from 'assets/images/svg/arrow.svg';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import { Title, TextBody } from 'ui/atoms';
import { device } from 'lib/constants';

interface CarouselType {
  id: number;
  image: string;
  title: string;
  description: string;
}

export const Carousel: React.FC<{ items: CarouselType[] }> = ({ items }) => {
  var settings = {
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    nextArrow: (
      <Arrow isRotated>
        <img src={arrow} alt="next-arrow" />
      </Arrow>
    ),
    prevArrow: (
      <Arrow>
        <img src={arrow} alt="prev-arrow" />
      </Arrow>
    ),
  };
  return (
    <Slider {...settings}>
      {items.map(({ id, title, description, image }) => (
        <Slide key={id}>
          <div>
            <PhoneImg src={image} />
          </div>
          <SlideText>
            <SubTitleSlider>
              <Title size="s">{title}</Title>
            </SubTitleSlider>
            <SliderDescription>
              <TextBody color="basic" size="s">
                {description}
              </TextBody>
            </SliderDescription>
          </SlideText>
        </Slide>
      ))}
    </Slider>
  );
};

interface MapProps {
  isRotated?: boolean;
}

const map = (props: MapProps) => ({
  'data-rotated': props.isRotated,
});

export const Arrow = styled.div.attrs(map)<MapProps>`
  img {
    width: 50px;
    height: 50px;
  }

  &[data-rotated='true'] {
    img {
      transform: rotate(180deg);
    }
  }
  @media ${device.tablet} {
    img {
      width: 96px;
      height: 96px;
    }
  }
`;

export const SlideText = styled.div`
  padding-top: 85px;
  width: 290px;
  @media ${device.tablet} {
    padding-top: 0px;
    margin-top: -35px;
    width: 562px;
  }
  @media ${device.desktop} {
    width: 1000px;
  }
`;

export const SliderDescription = styled.div`
  padding-bottom: 30px;
  @media ${device.tablet} {
    margin: 0 auto;
    width: 518px;
    padding-bottom: 78px;
  }

  @media ${device.desktop} {
    width: 795px;
    margin: 0 auto;
    padding-bottom: 49px;
  }
`;

export const SubTitleSlider = styled.div`
  padding-bottom: 9px;
  white-space: pre-wrap;
  div {
    width: 246px;
  }
  @media ${device.tablet} {
    padding-bottom: 15px;
  }
`;

export const Slide = styled.div`
  display: flex;
  justify-content: center;
`;

export const PhoneImg = styled.img`
  width: 287px;
  height: 180px;
  background-size: cover;
  margin: 0 auto;
  @media ${device.tablet} {
    width: 480px;
    height: 296px;
  }
  @media ${device.desktop} {
    width: 657px;
    height: 405px;
  }
`;
